<template>
  <div
    class="headerBox boxBorder"
    :class="active"
    @mouseover="mouseOver"
    @mouseleave="mouseLeave"
  >
    <img src="@/assets/decorateBox/back.png" alt />
    <p>{{itemData.title}}</p>
    <img class="opc" src="@/assets/decorateBox/back.png" alt />
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "" //绑定样式
    };
  },
  methods: {
    // 移入事件
    mouseOver() {
      this.active = "active";
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

<style scoped lang="less">
.opc{
  opacity: 0;
}
.headerBox{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  background-color: #F7F7F7;
  height: 40px;
  font-size: 17px;
  color: #030303;
}
</style>
